<style lang="less" scoped>
  @import './subject.less';
</style>

<template>
<div class="bg_ff">

    <Row class="app-resource-group-title">
         <h3></h3>
    </Row>
    <Row class="subject-content">
         <Table :columns="columns" :data="subjects"  size="small"></Table>
    </Row>

  </div>
</template>

<script>
import { getAppSubjectData,openOrForbiddenSubject } from "@/services/appService.js";

export default {
  name: "appSubject",
  components: {},
  props: {
    isAdmin: Boolean
  },
  data() {
    return {
      listId: this.$route.params.listId,
      columns: [
        {
          title: "",
          key: "accountName",
          align: "left"
        },
        {
          title: "",
          key: "classify",
          align: "left",
          render: (h, params) => {
            if (params.row.classify === 1) {
              return h("span", {}, "");
            } else {
              return h("span", {}, "");
            }
          }
        },
        {
          title: "",
          key: "dataCollection",
          align: "left",
          render: (h, params) => {
            let collectionStatus = [],
              subject = params.row;

            //
            subject.calcRels.forEach(val => {
              collectionStatus.push(val.matchDirectionByDebit);
            });

            // 
            if (subject.classify === 1) {
              if (/1/.test(collectionStatus) && /-1/.test(collectionStatus)) {
                return h("span", {}, "");
              } else if (/1/.test(collectionStatus)) {
                return h("span", {}, "");
              } else if (/-1/.test(collectionStatus)) {
                return h("span", {}, "");
              }
            } else {
              if (/1/.test(collectionStatus) && /-1/.test(collectionStatus)) {
                return h("span", {}, "");
              } else if (/1/.test(collectionStatus)) {
                return h("span", {}, "");
              } else if (/-1/.test(collectionStatus)) {
                return h("span", {}, "");
              }
            }
          }
        },
      ],
      subjects: []
    };
  },
  watch: {
    isAdmin: function(value) {
      const lastColumn = {
          title: "",
          key: "key",
          align: "center",
          render: (h,params) => {
            let isDisabled = false,isChecked = false;
            if(params.row.classify === 1){
              isDisabled = true;
            }
            if(params.row.calcRels[0].status === 1){
              isChecked = true;
            }
            return h('Checkbox',{
              props: {
                disabled: isDisabled,
                value: isChecked
              },
              on: {
                'on-change': (status) => {
                  this.renderMethod(status,params);
                }
              }
            })
          }
      };
      if(value){
        if(this.columns[this.columns.length-1].title !== ''){
          this.columns.push(lastColumn);
        }
      }else{
        if(this.columns[this.columns.length-1].title === ''){
          this.columns.splice(this.columns.length-1,1);
        }
      }
    }
  },
  methods: {
    //columns
    rerenderSubjectColumns() {
      this.columns[this.columns.length-1].render = (h,params) => {
          let isDisabled = false,isChecked = false;
          if(params.row.classify === 1){
            isDisabled = true;
          }
          if(params.row.calcRels[0].status === 1){
            isChecked = true;
          }
          return h('Checkbox',{
            props: {
              disabled: isDisabled,
              value: isChecked
            },
            on: {
              'on-change': (status) => {
                this.renderMethod(status,params);
              }
            }
          })
        }
    },
    //
    renderMethod(status,params) {
      let msgContent = '';
      if(status){
        msgContent = ''
      }else{
        msgContent = ''
      }
      this.$Modal.confirm({
        title: '',
        content: msgContent,
        onOk: () => {
          params.row.calcRels.forEach(val => {
            openOrForbiddenSubject(val.componentId).then(res => {
              if(res.status === 200){
                this.$Message.success('');
              }
            })
          });
        },
        onCancel: () => {
          this.rerenderSubjectColumns(); 
        }
      });
    }
  },
  created() {
    getAppSubjectData(this.listId).then(res => {
      if (res.status === 200) {
        this.subjects = res.data;
      } else {
        //todo
        //
      }
    });
  }
};
</script>




